import React, { useEffect } from 'react'
import { TransactionTable, RawDataTransactionItem, TransactionTradeTypes, TransactionSide, TransactionStatus } from '@loopring-web/component-lib'
import { WithTranslation, withTranslation } from 'react-i18next'
import store from 'stores'
import { LoopringAPI } from 'stores/apis/api'
import { volumeToCount } from 'hooks/help'
import { StylePaper } from '../../styled'


const TxPanel = withTranslation('common')((rest:WithTranslation<'common'>) => {
    const container = React.useRef(null);
    const [pageSize, setPageSize] = React.useState(10);
    const [txTableData, setTxTableData] = React.useState<RawDataTransactionItem[]>([]);

    const { accountId,apiKey } = store.getState().account;

    useEffect(() => {
        (async function getUserApi () {
            if (LoopringAPI && LoopringAPI.userAPI && accountId && apiKey) {
                const userTxns = await LoopringAPI.userAPI.getUserTranferList({
                    accountId,
                }, apiKey)
                const mappingList = userTxns.userTransfers.map(o => ({
                  token: o.symbol,
                  from: o.senderAddress,
                  to: o.receiverAddress,
                  // amount: Number(o.amount),
                  amount: Number(volumeToCount(o.symbol, o.amount)),
                  fee: {
                    unit: o.feeTokenSymbol || '',
                    value: Number(volumeToCount(o.symbol, o.feeAmount || 0))
                  },
                  memo: o.memo || '',
                  time: o.timestamp,
                  txnHash: o.hash,
                  status: o.status === 'processed' ? TransactionStatus.processed : o.status === 'processing' ? TransactionStatus.processing : o.status === 'received' ? TransactionStatus.received : TransactionStatus.failed,
                  tradeType: o.txType === 'TRANSFER' ? TransactionTradeTypes.transfer : o.txType === 'DEPOSIT' ? TransactionTradeTypes.deposit : TransactionTradeTypes.withdraw,
                }))
                setTxTableData(mappingList)
            }
        })()
    }, [accountId, apiKey])

    React.useEffect(() => {
        // @ts-ignore
        let height = container?.current?.offsetHeight;
        if (height) {
            setPageSize(Math.floor((height - 120) / 44) - 2);
        }
    }, [container, pageSize]);

    return (
        <StylePaper ref={container}>
            <div className="title">Transactions</div>
            <div className="tableWrapper">
                <TransactionTable {...{
                  // rawData: txList,
                  rawData: txTableData,
                  pagination: {
                    pageSize: pageSize
                  },
                  showFilter: true,
                  ...rest
                }} />
            </div>
        </StylePaper>
    )
})

export default TxPanel
